<template>
  <div id="linkList" class="clearfix">
    <ul class="pull-left">
      <router-link tag="li" to="/page/film">影院热映</router-link>
      <router-link tag="li" to="/page/book">畅销图书</router-link>
    </ul>
    <ul class="pull-right">
      <router-link tag="li" to="/register">注册账号</router-link>
      <router-link tag="li" to="/login">登录豆瓣</router-link>
    </ul>
    <div class="horizontal"></div>
    <div class="vertical"></div>
  </div>
</template>

<script>
export default {
}
</script>

<style scoped>
#linkList {
  position: relative;
}

#linkList ul {
  width: 50%;
}

#linkList li {
  height: 4rem;
  line-height: 4rem;
  color:#333;
  font-size: 1.4rem;
  background: #f6f6f6;
  text-align: center;
}

#linkList ul li:first-child {
  padding-bottom: 3px;
}

#linkList ul li:last-child {
  padding-top: 3px;
}

#linkList .horizontal {
  position: absolute;
  top:50%;
  margin-top: -.3rem;
  left:0;
  width: 100%;
  height: .6rem;
  background-color: #fff;
}

#linkList .vertical {
  position: absolute;
  top:0;
  left:50%;
  width: .6rem;
  height: 100%;
  background-color: #fff;
  margin-left: -.3rem;
}

</style>



