<template>
	<div class="share clearfix">
		<!-- JiaThis Button BEGIN -->
		<div class="jiathis_style_32x32">
			<a class="jiathis_button_weixin"><img src="../assets/picture/wx.png" alt=""/></a>
			<a class="jiathis_button_tsina"><img src="../assets/picture/wb.png" alt=""/></a>
			<a class="jiathis_button_cqq"><img src="../assets/picture/qq.png" alt=""/></a>
			<a class="jiathis_button_qzone"><img src="../assets/picture/qzone.png" alt=""/></a>
		</div>
		<!-- JiaThis Button END -->
	</div>
</template>

<script>
export default {
  mounted: function() {
      this.init();
  },
  methods: {
	  init: function () {
	    let url = 'http://v3.jiathis.com/code/jia.js',
	    	script = document.createElement('script');
	    script.setAttribute('src', url);
	    document.getElementsByTagName('head')[0].appendChild(script);
	  }
  },
  computed: {
  	// changeURL: function(){
  	// 	this.wbURL = "http://v.t.sina.com.cn/share/share.php?title="+this.socail.title+"&url="+this.socail.url;
  	// 	this.qqURL = "http://v.t.qq.com/share/share.php?title="+this.socail.title+"&url="+this.socail.url;
  	// 	this.qzoneURL = "http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url="+this.socail.title+"&url="+this.socail.url;
  	// }
  }
}
</script>

<style scoped>
.share a {
	float: left;
	width: 2.5rem;
	height: 2.5rem;
	position: relative;
	overflow: hidden;
	border-radius: 2.5rem;
}

.share a + a {
	margin-left: 1rem;
}

.share a img {
	position: absolute;
	top:0;
	left:0;
	width: 100%;
	border-radius: 2.5rem;
	background-color: #fff;
}

</style>



