<template>
  <div class="login">
    <div class="header">
      <span @click="back">取消</span>
      <h2>登录豆豆</h2>
    </div>
    <div class="content">
      <input type="text" placeholder="邮箱/手机号/用户名" />
      <input type="text" placeholder="密码" />
      <div>登 录</div>
    </div>
    <div class="footer">
      <router-link tag="span" to="/register">注册豆豆账号</router-link>
      <!-- <router-link tag="span" to="/">下载豆瓣App</router-link> -->
    </div>
  </div>
</template>

<script>

export default {
  methods: {
    back: function(){
      this.$router.go(-1);
    }
  }
}
</script>

<style scoped>
.login .header {
  position: relative;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  text-align: center;
}

.login .header span {
  position: absolute;
  top:0;
  left:15px;
  color:#42bd56;
  font-size: 16px;
}

.login .header h2 {
  font-weight: bold;
  font-size: 20px;
}

.login .content {
  padding:30px 15px 0 15px;
}

.login .content input {
  width: 100%;
  border:1px solid #ccc;
  margin-top:-1px;
  padding: 10px 5px;
  font-size: 16px;
}

.login .content input:first-of-type {
  border-radius:3px 3px 0 0;
}

.login .content input:last-of-type {
  border-radius:0;
}

.login .content div {
  width: 100%;
  padding: 10px 5px;
  font-size: 16px;
  border-radius:3px;
  background-color: #42bd56;
  color:#fff;
  margin-top: 10px;
  text-align: center;
}

.login .footer {
  margin-top: 40px;
  text-align: center;
}

.login .footer span {
  font-size: 16px;
  color:#42bd56;
}

.login .footer span+span {
  margin-left: 20px;
}
</style>



