// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VRouter from 'Vue-router'
import index from "./pages/index"
import film from "./pages/film"
import book from "./pages/book"
import broadcast from "./pages/broadcast"
import group from "./pages/group"
import login from "./pages/login"
import register from "./pages/register"
import page from "./pages/page"
import most from "./pages/most"
import get from "./pages/get"
import search from "./pages/search"
import filmDetail from "./pages/filmDetail"
import bookDetail from "./pages/bookDetail"
import musicDetail from "./pages/musicDetail"
import theatresMore from './pages/theatresMore'
import top250More from './pages/top250More'
import xugouMore from './pages/xugouMore'
import unxugouMore from './pages/unxugouMore'
import axios from 'axios'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import VueJsonp from 'vue-jsonp'
import fontSize from '../static/js/fontsize.js'
import adminAPI from '../static/js/public.js'
import { InfiniteScroll, Lazyload, Loadmore  } from 'mint-ui'
import FastClick from 'fastclick'
require('./mock.js')
require('./broadcast.js')

Vue.use(VRouter)
Vue.use(VueAwesomeSwiper)
Vue.use(InfiniteScroll)
Vue.use(Lazyload)
Vue.use(VueJsonp)
Vue.component('mtLoadmore', Loadmore);

Vue.prototype.$common = adminAPI
Vue.prototype.$http = axios
FastClick.attach(document.body);

let router = new VRouter({
	routes: [
		{
			path: '/',
			redirect: '/page/index',
			component: page,
			children: [
				{
					path: '/page/index',
					component: index
				},
				{
					path: '/page/film',
					component: film
				},
				{
					path: '/page/book',
					component: book
				},
				{
					path: '/page/broadcast',
					component: broadcast
				},
				{
					path: '/page/group',
					component: group
				},
				{
					path: '/page/most',
					component: most,
					children: [
						{
							path: '/page/most/theatresMore',
							component: theatresMore
						},
						{
							path: '/page/most/top250More',
							component: top250More
						},
						{
							path: '/page/most/xugouMore',
							component: xugouMore
						},
						{
							path: '/page/most/unxugouMore',
							component: unxugouMore
						}
					]
				},
				{
					path: '/page/filmDetail/:id',
					name: 'filmDetail',
					component: filmDetail
				},
				{
					path: '/page/bookDetail/:id',
					name: 'bookDetail',
					component: bookDetail
				},
				{
					path: '/page/musicDetail/:id',
					name: 'musicDetail',
					component: musicDetail
				}
			]
		},
		{
			path: '/login',
			component: login
		},
		{
			path: '/register',
			component: register
		},
		{
			path: '/search',
			component: search
		},
		{
			path: '/get',
			name: 'get',
			component: get
		}
	]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  router,
  components: { App }
})



