<template>
 <div v-if="star == 0?false:true" class="star-five clearfix">
	<p v-for="item in arr1"><img class='star-img' src="../assets/picture/zan1.png" alt=""/></p>
	<p v-for="item in arr2"><img class='star-img' src="../assets/picture/zan2.png" alt=""/></p>
	<p v-for="item in arr3"><img class='star-img' src="../assets/picture/zan3.png" alt=""/></p>
	{{score}}
</div>
<span v-else class="star-five-txt">暂无评论</span>
</template>

<script>
export default {
  props: ['star'],
  data: function(){
    return {  
    	arr1: [],
    	arr2: [],
    	arr3: []
    }
  },
  computed: {
	score: function(){
		var star = parseInt(this.star);
		var sum = Math.floor(star/10);
		for(let i = 0;i < sum;i++){
			this.arr1.push(i);
		}
		for(let i = 0;i<Math.floor((50-star)/10);i++){
			this.arr3.push(i);
		}
		for(let i = 0;i<5-this.arr1.length-this.arr3.length;i++){
			this.arr2.push(i);
		}
	}		
  }
}
</script>

<style scoped>
.star-five {
	display: inline-block;
	vertical-align: middle;
}

.star-five p {
	float: left;
	width: 1.1rem;
}

.star-five .star-img {
	width: 100%;
}

.star-five-txt {
	font-size: 1.2rem;
}

</style>



