<template>
  <!-- 虚构类 -->
  <div>
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">最受关注图书 | 虚构类</h2>
        <router-link tag="span" to="/page/most/xugouMore" class="pull-right">更多</router-link>
      </div>
      <swiper :options="swiperOption" class="content">
        <swiper-slide class="image-box" v-for="item in xugou" :key="item.id">
          <router-link :to="{name:'bookDetail',params:{id:item.id}}" tag="div">
            <div class="img-box">
              <img v-lazy="$common.getImage(item.images.medium)" :alt="item.alt"/>
            </div>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="(item.rating.average)*10/2"></five-star>
              {{item.rating.average == 0?'':item.rating.average}}
            </p>
          </router-link>
        </swiper-slide>
      </swiper>
    </div>
    <!-- 非虚构类 -->
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">最受关注图书 | 非虚构类</h2>
        <router-link tag="span" to="/page/most/unxugouMore" class="pull-right">更多</router-link>
      </div>
      <swiper :options="swiperOption" class="content">
        <swiper-slide class="image-box" v-for="item in unxugou" :key="item.id">
          <router-link :to="{name:'bookDetail',params:{id:item.id}}" tag="div">
            <div class="img-box">
              <img v-lazy="$common.getImage(item.images.medium)"  :alt="item.alt"/>
            </div>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="(item.rating.average)*10/2"></five-star>
              {{item.rating.average == 0?'':item.rating.average}}
            </p>
          </router-link>
        </swiper-slide>
      </swiper>
    </div>
    <!-- 发现好图书 -->
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">发现好图书</h2>
      </div>
      <swiper :options="swiperOption2" class="content">
        <swiper-slide class='good-film clearfix'>
            <p class="green">小波看书</p>
            <p class="blue">村上春树周边</p>
            <p class="green">我凭名字认定了你</p>
            <p class="yellow">不可饶恕的女人们</p>
          </swiper-slide>
          <swiper-slide class='good-film clearfix'>
            <p class="shenhong">爱欲书</p>
            <p class="blue">他们还写侦探小说</p>
            <p class="blue">人生识字始忧患</p>
            <p class="shenhong">诗歌书店</p>
          </swiper-slide>
      </swiper>
    </div>
    <film-class :link="link"></film-class>
    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import filmClass from "./filmClass"
import doubanFooter from "./footer"
import fivestar from "../components/fivestar"

export default {
  data: function(){
    return {
      xugou: [],
      unxugou: [],
      link : ['小说','爱情','历史','外国文学','青春','励志','随笔','传记','推理','旅行','奇幻','经管'],
      swiperOption: {
        slidesPerView: 'auto',
        freeMode: true
      },
      swiperOption2: {
        slidesPerView: 'auto',
        slidesPerColumn: 2,
        freeMode: true
      }
    }
  },
  components: {
    'film-class': filmClass,
    'douban-footer': doubanFooter,
    'five-star': fivestar
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    var self = this;
    this.$jsonp('https://api.douban.com/v2/book/search?q="虚构"').then(data => {
        for(var i =0;i<8;i++){
          self.xugou.push(data.books[i]);
        } 
    }).catch(err => {
    　　console.log(err)
    })
    this.$jsonp('https://api.douban.com/v2/book/search?q="非虚构"').then(data => {
        for(var i =0;i<8;i++){
          self.unxugou.push(data.books[i]);
        }
    }).catch(err => {
    　　console.log(err)
    })
  },
  methods: {
      // this.$http.get(url)
      // .then(function (response) {
      //   if(response.status === 200){
      //     for(var i =0;i<8;i++){
      //       obj.push(response.data.books[i]);
      //     }
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
  }
}
</script>

<style scoped>
@import url("../assets/css/swiper-3.4.2.min.css");

.film {
  margin-bottom: 20px;
}

.film .title {
  height: 2rem;
  line-height: 2rem;
}

.film .title h2 {
  font-size: 1.6rem;
}

.film .title span {
  font-size: 1.2rem;
  color:#42bd56;
}

.film .content {
  margin-top: 15px;
}

.film .content .image-box {
  text-align: center;
  width:10rem;
}

.film .content .image-box h2 {
  font-size: 1.4rem;
}

.film .content .image-box+.image-box {
  margin-left: 10px;
}

/*.img-box {
  width:10rem;
  height: 16rem;
  background-color: #666;
}*/

image[lazy=loading] {
  width:10rem;
  height: 16rem;
}

.film .content .image-box img {
  width:10rem;
  height: 16rem;
}

.film .content .image-box p {
  font-size: 16px;
  color:#aaa;
}

.good-film {
  width: 52rem;
}

.good-film {
  margin-bottom: 10px;
}

.good-film p{
  font-size: 1.4rem;
  padding:.5rem 1rem;
  border:1px solid #ccc;
  border-radius: 5px;
  float: left;
}

.good-film p + p {
  margin-left: .5rem;
}

.good-film .green {
  border-color: #42bd56;
  color:#42bd56;
}

.good-film .blue {
  border-color: #2384e8;
  color:#2384e8;
}

.good-film .fenhong {
  border-color: #e1644d;
  color:#e1644d;
}

.good-film .shenhong {
  border-color: #e6467e;
  color:#e6467e;
}

.good-film .yellow {
  border-color: #fe8f2e;
  color:#fe8f2e;
}
</style>



