import Mock from 'mockjs'

// 获取 mock.Random 对象
const Random = Mock.Random;
// mock一组数据
const produceNewsData = function() {
    let dataList = [];
    for (let i = 0; i < 50; i++) {
        let newArticleObject = {
            contentTitle: Random.ctitle(4,15), //  Random.csentence( min, max )
            contentPic: Random.dataImage('120x240', 'Mock.js'), // Random.dataImage( size, text ) 生成一段随机的 Base64 图片编码
            contentTxt: Random.cparagraph(2,3), // Random.cname() 随机生成一个常见的中文姓名
            contentTime: Random.datetime() // Random.date()指示生成的日期字符串的格式,默认为yyyy-MM-dd；Random.time() 返回一个随机的时间字符串
        }
        dataList.push(newArticleObject)
    }
 
    return {
        dataList: dataList
    }
}

Mock.mock('/news/index', 'get', produceNewsData);


