<template>
  <div v-if="bookDetail == null?false:true" class="book-detail">
    <h1>{{bookDetail.title}}</h1> 

    <!-- 简介 -->
    <div class="summary clearfix">
      <div class="summary-left">
        <five-star :star="bookDetail.rating.average*10/2"></five-star>
        {{bookDetail.rating.average == 0?"":bookDetail.rating.average}}
        <span class="summary-ratings-count">{{bookDetail.rating.numRaters}}人评价</span>
        <p class="summary-genres">
          <span>{{bookDetail.author[0]}}</span> / / {{bookDetail.publisher}} / {{bookDetail.pages}} / {{bookDetail.binding}} / {{bookDetail.price}} / {{bookDetail.pubdate}}
        </p>
      </div>
      <div class="summary-right"><img :src="$common.getImage(bookDetail.images.medium)" alt=""/></div>
    </div>

    <!-- 社交分享 -->
    <share></share>

    <div class="btn clearfix">
      <button class="pull-left">想读</button>
      <button class="pull-left">在读</button>
      <button class="pull-left">读过</button>
    </div> 
    
    <!-- 简介详细  -->
    <div class="summary-detail">
      <h2>{{bookDetail.title}}的剧情简介</h2>
      <p>{{bookDetail.summary}}</p>
    </div>

    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import doubanFooter from "./footer"
import share from "../components/share"
import { Indicator } from 'mint-ui'

export default {
  data: function(){
    return {
      bookDetail: null
    }
  },
  created: function(){
    this.$common.scrollToTop();
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.bookDetail = null;
    })
  },
  mounted: function(){
    var id = this.$route.params.id,
        self = this;
    Indicator.open();
    this.$jsonp('https://api.douban.com/v2/book/'+id).then(data => {
        self.bookDetail = data;
        Indicator.close();
    }).catch(err => {
    　　console.log(err)
    })
  },
  activated: function(){
    this.$common.scrollToTop();
    var id = this.$route.params.id,
        self = this;
    Indicator.open();
    this.$jsonp('https://api.douban.com/v2/book/'+id).then(data => {
        self.bookDetail = data;
        Indicator.close();
    }).catch(err => {
    　　console.log(err)
    })
  },
  methods: {
      // this.$http.get(url)
      // .then((response) => {
      //   if(response.status == 200){
      //     obj.push(response.data);
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    // }
  },
  components: {
    'five-star': fivestar,
    'douban-footer': doubanFooter,
    'share': share
  }
}
</script>

<style scoped>
.book-detail h1 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.summary .summary-left {
  width: 65%;
  font-size: 1.2rem;
  float: left;
}

.summary .summary-left .summary-ratings-count {
  color:#aaa;
}

.summary-genres {
  line-height: 1.5;
  margin-top: 10px;
}

.summary-genres span {
  color: #42bd56;
}

.summary .summary-right {
  width: 30%;
  float: right;
}

.summary .summary-right img {
  width: 100%;
}

.btn {
  margin: 60px 0 30px 0;
}

.btn button {
  width: 32%;
  padding:5px 0;
  background-color: #fff;
  border: 1px solid #EEB422;
  color:#EEB422;
  border-radius: 5px;
  font-size: 1.3rem;
}

.btn button + button {
  margin-left: 2%;
}

.summary-detail h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin-bottom: 15px;
}

.summary-detail p {
  font-size: 1.3rem;
  line-height: 1.6;
}
</style>



