<template>
  <!-- 影院热映 -->
  <div>
    <div class="group">
      <div class="login">
        <img src="../assets/picture/person.jpg" alt=""/>
        <span @click="isLayout=true;isShow=true;">请先登录</span>
      </div>
      <div class="group-content">
        <div v-for="(key,index) in count" :key="index" class="group-detail">
          <h2>{{key.title}}</h2>
          <div @click="isLayout=true;isShow=true;" v-for="item in key.type" :key="item.number" class="group-box">
            <div class="clearfix">
              <div class="pull-left">
                <img :src="item.img" alt=""/>
                <span class="nowrap">{{item.title}}</span>
              </div>
              <h3 class="pull-right">{{item.number}}人</h3>
            </div>
            <p class="nowrap">{{item.info}}</p>
          </div>
          <h4 @click="isLayout=true;isShow=true;">{{key.more}}</h4>
        </div>
      </div>
    </div>   
    <footer>
      <douban-footer></douban-footer>
    </footer>
    <!-- 弹窗 -->
    <lay-out :isLayout="isLayout" :state="state" @close-layout="close">
      <div :slot="state" class="login-layer">
          <h2>继续操作需要登录豆豆账号</h2>
          <router-link to="/login" tag="button">登录</router-link>
          <!-- <input type="button" value="打开 App"/> -->
          <router-link to="/register" tag="button">注册豆豆账号</router-link>
      </div>
    </lay-out>
    <transition name="mask">
      <mask-layer v-show="isShow" @click.native="isLayout=false;isShow=false"></mask-layer>
    </transition>
  </div>
</template>

<script>
import doubanFooter from "./footer"
import layout from "../components/layout"
import maskLayer from "../components/maskLayer"
export default {
  data: function(){
    return {
      isLayout: false,
      isShow: false,
      state: 'login',
      count: [
        {
          title: '租房找室友',
          more: '更多相关小组',
          type: [
            {
              title: '成都租房',
              img: require('../assets/picture/g117205-2.jpg'),
              info:'3分钟前更新：天府四街临江苑套一出租个人 无中介费噢',
              number: 1230
            },
            {
              title: '北京租房（非中介）',
              img: require('../assets/picture/g279962-3.jpg'),
              info:'9号线丰台南路站看丹桥新华街四里，次卧出租，押一付一',
              number: 4234
            },
            {
              title: '深圳福田租房（个人房源免费推广）',
              img: require('../assets/picture/g117205-2.jpg'),
              info:'福田福民岗厦复式单间整租，2800，可短租',
              number: 23324
            }
          ],
        },
        {
          title: '来聊五块钱',
          more: '来聊五块钱',
          type: [
            {
              title: '想不想很多人帮你！',
              img: require('../assets/picture/g14859-17.jpg'),
              info:'想转专业去学日语大家有神马建议？',
              number: 4224
            },
            {
              title: '我们都有秘密',
              img: require('../assets/picture/g17947-8.jpg'),
              info:'1分钟前更新：北京征个女生',
              number: 234
            },
            {
              title: '文艺青年装逼会',
              img: require('../assets/picture/g17947-8.jpg'),
              info:'3分钟前更新：文艺青年装逼会',
              number: 3452
            }
          ],
        },
        {
          title: '买买买',
          more: '更多相关小组',
          type: [
            {
              title: '我爱代购&amp;海淘☺微信号',
              img: require('../assets/picture/g573696-2.jpg'),
              info:'4分钟前更新：靠谱小日代 薇信直播定位+邮寄单号 可拼邮可直邮 保证品 ～笔芯',
              number: 3535
            },
            {
              title: 'PiaPia团✿团购',
              img: require('../assets/picture/g25704-22.jpg'),
              info:'1小时前更新：【想买】之前团过的包要吗？',
              number: 54546
            },
            {
              title: '我们一起海淘',
              img: require('../assets/picture/g573696-2.jpg'),
              info:'3分钟前更新：靠谱小日代 薇信直播定位+邮寄单号 可拼邮可直邮 保正品～笔芯',
              number: 4546
            }
          ],
        },
      ]
    }
  },
  components: {
    'douban-footer': doubanFooter,
    'lay-out': layout,
    'mask-layer': maskLayer
  },
  mounted: function(){
    this.$common.scrollToTop();
  },
  methods: {  
    close: function(pra){
      this.isLayout = pra;
      this.isShow = pra;
    }
  }
}
</script>

<style scoped>
.login {
  padding:15px 0;
  border-bottom: 1px solid #ddd;
}

.login img {
  width: 4rem;
  border-radius: 4rem;
  vertical-align: middle;
}

.login span {
  font-size: 1.3rem;
  vertical-align: middle;
  margin-left: 10px;
  color:#aaa;
}

.group-content {
  margin-top: 20px;
}

.group-detail h2 {
  font-size: 1.5rem;
}

.group-detail h4 {
  text-align: center;
  color:#42bd56;
  font-size: 1.3rem;
  padding-top: 20px;
}

.group-detail+.group-detail {
  margin-top: 60px;
}

.group-box {
  padding: 15px 0;
  border-bottom: 1px solid #ddd;
}

.group-box>div {
  height: 48px;
}

.group-box .pull-left {
  width: 75%;
}

.group-box .pull-right {
  width: 20%;
}

.group-box img {
  height: 48px;
  vertical-align: middle;
}

.group-box span {
  display: inline-block;
  width: 70%;
  font-size: 1.4rem;
  vertical-align: middle;
  margin-left: 10px;
}

.group-box h3 {
  font-size: 1.3rem;
  color:#aaa;
  line-height: 48px;
  text-align: right;
}

.group-box p {
  font-size: 1.3rem;
  color:#aaa;
  margin-top: 10px;
}

.login-layer {
  text-align: center;
}

.login-layer h2 {
  font-size: 1.5rem;
  padding-bottom: 25px;
}

.login-layer button,.login-layer input {
  width: 100%;
  padding:5px 0;
  font-size: 1.3rem;
  border-radius: 5px;
}

.login-layer button {
  color:#fff;
  background-color:#24be2f;
  margin-bottom: 10px;
}

.login-layer input {
  background-color: #fff;
  color:#24be2f;
  border:1px solid #24be2f;
  margin-bottom: 15px;
}

.login-layer span {
  font-size: 1.3rem;
  color:#24be2f;
}

.mask-enter-active,.mask-leave-active {
  transition: opacity .3s ease;
}

.mask-leave-active {
  transition: opacity .1s linear .4s;
}

.mask-enter,.mask-leave-active {
  opacity: 0;
}
</style>



