<template>
  <div>
    <!-- 影院热映 -->
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">影院热映</h2>
        <router-link to="/page/most/theatresMore" tag="span" class="pull-right">更多</router-link>
      </div>
      <swiper :options="swiperOption" class="content">
        <swiper-slide class="image-box" v-for="item in theatres" :key="item.id">
          <router-link :to="{name:'filmDetail',params:{id:item.id}}" tag="div">
            <div><img v-lazy="$common.getImage(item.images.medium)" :alt="item.alt"/></div>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="item.rating.stars"></five-star>
              {{item.rating.average == 0?'':item.rating.average}}
            </p>
          </router-link>
        </swiper-slide>
      </swiper>
    </div>
    <!-- 豆瓣top250 -->
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">豆瓣top250</h2>
        <router-link to="/page/most/top250More" tag="span" class="pull-right">更多</router-link>
      </div>
      <swiper :options="swiperOption" class="content">
        <swiper-slide class="image-box" v-for="item in top250" :key="item.id">
          <router-link :to="{name:'filmDetail',params:{id:item.id}}" tag="div">
            <img v-lazy="$common.getImage(item.images.small)" :alt="item.alt"/>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="item.rating.stars"></five-star>
              {{item.rating.average == 0?'':item.rating.average}}
            </p>
          </router-link>
        </swiper-slide>
      </swiper>
    </div>
    <!-- 发现好电影 -->
    <div class="film">
      <div class="title clearfix">
        <h2 class="pull-left">发现好电影</h2>
      </div>
      <swiper :options="swiperOption2" class="content">
          <swiper-slide class='good-film clearfix'>
            <p class="green">同时入选IMDB250和豆瓣电影250的电影
            </p>
            <p class="blue">带你进入不正常的世界</p>
            <p class="fenhong">用电【影】来祭奠逝去的岁月</p>
            <p class="shenhong">女孩们的故事【电影】</p>
          </swiper-slide>
          <swiper-slide class='good-film clearfix'>
            <p class="green">使用App【找电影】功能</p>
            <p class="blue">科幻是未来的钥匙--科幻启示录【科幻题材】</p>
            <p class="green">美国生活面馆</p>
            <p class="yellow">2015终极期待</p>
            <p class="yellow">经典韩国电影--收集100部</p>
          </swiper-slide>
      </swiper>
    </div>
    <film-class :link="link"></film-class>
    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import filmClass from "./filmClass"
import doubanFooter from "./footer"
import fivestar from "../components/fivestar"

export default {
  data: function(){
    return {
      theatres: [],
      top250: [],
      link : ['经典','冷门佳片','豆瓣高分','动作','喜剧','爱情','悬疑','恐怖','科幻','治愈','文艺','成长','动画','华语','欧美','韩国','日本'],
      swiperOption: {
        slidesPerView: 'auto',
        freeMode: true
      },
      swiperOption2: {
        slidesPerView: 'auto',
        slidesPerColumn: 2,
        freeMode: true
      }
    }
  },
  components: {
    'film-class': filmClass,
    'douban-footer': doubanFooter,
    'five-star': fivestar
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    var self = this;
    this.$jsonp('https://api.douban.com/v2/movie/in_theaters').then(data => {
        for(var i =0;i<8;i++){
          self.theatres.push(data.subjects[i]);
        } 
    }).catch(err => {
    　　console.log(err)
    })
    this.$jsonp('https://api.douban.com/v2/movie/top250').then(data => {
        for(var i =0;i<8;i++){
          self.top250.push(data.subjects[i]);
        }
    }).catch(err => {
    　　console.log(err)
    })
  },
  methods: {
      // this.$http.get(url)
      // .then(function (response) {
      //   if(response.status === 200){
      //     for(var i =0;i<8;i++){
      //       obj.push(response.data.subjects[i]);
      //     }
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
  }
}
</script>

<style scoped>
@import url("../assets/css/swiper-3.4.2.min.css");
.film {
  margin-bottom: 20px;
}

.film .title h2 {
  font-size: 1.6rem;
}

.film .title span {
  font-size: 1.2rem;
  color:#42bd56;
}

.film .content {
  margin-top: 15px;
}

.film .content .image-box {
  text-align: center;
  width:10rem;
}

.film .content .image-box h2 {
  font-size: 1.4rem;
}

.film .content .image-box+.image-box {
  margin-left: 10px;
}

.film .content .image-box img {
  width:10rem;
  height: 16rem;
}

.film .content .image-box p {
  font-size: 16px;
  color:#aaa;
}

.good-film {
  width: 90rem;
}

.good-film {
  margin-bottom: 10px;
}

.good-film p{
  font-size: 1.4rem;
  padding:.5rem 1rem;
  border:1px solid #ccc;
  border-radius: 5px;
  float: left;
}

.good-film p + p {
  margin-left: .5rem;
}

.good-film .green {
  border-color: #42bd56;
  color:#42bd56;
}

.good-film .blue {
  border-color: #2384e8;
  color:#2384e8;
}

.good-film .fenhong {
  border-color: #e1644d;
  color:#e1644d;
}

.good-film .shenhong {
  border-color: #e6467e;
  color:#e6467e;
}

.good-film .yellow {
  border-color: #fe8f2e;
  color:#fe8f2e;
}
</style>



