<template>
  <!-- 虚构类 -->
  <div v-if="show.length == 0?false:true" class="most">
      <h2 class="title">虚构类</h2>  
      <ul
      v-infinite-scroll="loadMoreData"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10" class="clearfix">
        <router-link :to="{name:'bookDetail',params:{id:item.id}}" tag="li" class="image-box pull-left" v-for="item in show" :key="item.id">
          <div class="content-box">
            <div class="img-box"><img v-lazy="$common.getImage(item.images.medium)" :alt="item.alt"/></div>
            <h2 class="nowrap">{{item.title}}</h2>
            <p>
              <five-star :star="(item.rating.average)*10/2"></five-star>
              <span>{{item.rating.average == 0?'':item.rating.average}}</span>
            </p>
          </div>
        </router-link>
      </ul>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import { Indicator } from 'mint-ui'

export default {
  data: function(){
    return {
      loading: false,
      xugou: [],
      show: []
    }
  },
  components: {
    'five-star': fivestar
  },
  created: function(){
    this.$common.scrollToTop();
  },
  mounted: function(){
    var self = this;
    Indicator.open();
    this.$jsonp('https://api.douban.com/v2/book/search?q="虚构"').then(data => {
        for(var i =0;i<data.books.length;i++){
          self.xugou.push(data.books[i]);
        } 
        Indicator.close();
    }).catch(err => {
    　　console.log(err)
    })
  },
  watch: {
    xugou: function(par){
      for(let i =0;i<9;i++){
        this.show.push(par[i]);
      }
    }
  },
  methods: {
    loadMoreData: function(){
      let showLength = this.show.length;
      let max = Math.min(showLength+9,this.xugou.length);
      for (let i = this.show.length; i < max; i++) {
        this.show.push(this.xugou[i]);
      }
      if(showLength+9 > this.xugou.length ){
        this.loading = true;
      }
    }
  }
}
</script>

<style>
</style>



