<template>
  <transition name="layer">
    <div v-show="isLayout" class="layout-box">
      <div id="del" @click="close">x</div>
      <slot :name="state"></slot>
    </div>
  </transition>
</template>

<script>
export default {
  props: ['isLayout','state'],
  data: function () {
    return {
      isClose: false
    }
  },
  methods: {
    close:function(){
      this.$emit('close-layout',this.isClose);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/ 
.layout-box {
  position: fixed;
  bottom:50%;
  left:50%;
  transform: translate(-50%,50%);
  width: 80%;
  padding:30px 20px;
  background-color: #fff;
  z-index:99999;
  line-height: 1;
  border-radius: 5px;
}

#del {
  position: absolute;
  right: 10px;
  top:10px;
  width: 15px;
  height: 15px;
  cursor: pointer;
  text-align: right;
}

#del:hover {
  color:#4fc08d;
}

.layer-enter-active,.layer-leave-active {
  transition: all .3s ease;
}

.layer-leave-active {
  transition: all .1s linear;
}

.layer-enter,.layer-leave-active {
  bottom:0;
  transform: translate(-50%,100%);
}

</style>



