<template>
  <div>
    <douban-header></douban-header>
    <div id="content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>  
  </div>
</template>

<script>
import header from "./header"
export default {
  components: {
    'douban-header': header
  }
}
</script>

<style>
#content {
  padding:5.5rem 20px 0 20px;
}
</style>



