<template>
  <div class="register">
    <div class="header">
      <h2>欢迎加入豆瓣</h2>
    </div>
    <div class="content">
      <input type="text" placeholder="手机号/邮箱" />
      <input type="text" placeholder="密码(最少6位)" />
      <input type="text" placeholder="昵称" />
      <div>下一步</div>
      <p>点击[下一步]代表你已阅读并同意<a href="javascript:;">用户使用协议</a></p>
    </div>
    <!-- <div class="footer">
      <router-link tag="span" to="/">下载豆瓣App</router-link>
    </div> -->
  </div>
</template>

<script>

export default {

}
