<style scoped>
.register .header h2 {
  padding:40px 0 30px 0;
  color:#42bd56;
  font-size: 30px;
  text-align: center;
}

.register .content {
  padding:0 15px 0 15px;
}

.register .content input {
  width: 100%;
  border:1px solid #ccc;
  margin-top:-1px;
  padding: 10px 15px;
  font-size: 16px;
  border-radius:0;
}

.register .content input:first-of-type {
