  padding: 10px 5px;
  font-size: 16px;
  border-radius:3px;
  background-color: #42bd56;
  color:#fff;
  margin-top: 10px;
  text-align: center;
}

.register .content p {
  color:#ccc;
  font-size: 14px;
  text-align: center;
  margin-top: 15px;
}

.register .content p a {
  color:#ccc;
  text-decoration: underline;
}

/*.register .footer {
  margin-top: 40px;
  text-align: center;
}

.register .footer span {
  font-size: 16px;
  color:#42bd56;
}*/

