<template>
  <!-- 影院热映 -->
  <div>
    <div class="broadcast" @touchstart="hideJubao">
      <div class="login">
        <img src="../assets/picture/person.jpg" alt=""/>
        <span @click="isLayout=true;isShow=true;">登录发广播</span>
      </div>
      <div v-if="defaultList.length === 0?false:true">
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
          <div v-for="(item,index) in defaultList" :key="index" class="comment clearfix">
            <img src="../assets/picture/download.png" class="pull-left" alt=""/>
            <div class="comment-content pull-right">
              <h2><strong>豆瓣</strong>说：</h2>
              <h3>{{item.contentTime}}</h3>
              <p>{{item.contentTxt}}</p>
              <img :src="item.contentPic" alt=""/>
              <div class="comment-foot clearfix">
                <div @click="isLayout=true;isShow=true;" class="zan pull-left">
                  <img src="../assets/picture/zan4.png" alt=""/>
                  <span>{{item.contentNum}}</span>
                </div>
                <div class="jubao pull-right">
                  <span @click="showJubao($event)">. . .</span>
                  <div class="jubao-box" @click="isLayout=true;isShow=true;">举报</div>
                </div>
              </div>
            </div>
          </div>
        </mt-loadmore>
      </div>
    </div>   
    <!-- 弹窗 -->
    <lay-out :isLayout="isLayout" :state="state" @close-layout="close">
      <div :slot="state" class="login-layer">
          <h2>继续操作需要登录豆瓣账号</h2>
          <router-link to="/login" tag="button">登录</router-link>
          <!-- <input type="button" value="打开 App"/> -->
          <router-link to="/register" tag="button">注册豆瓣账号</router-link>
      </div>
    </lay-out>
    <transition name="mask">
      <mask-layer v-show="isShow" @click.native="isLayout=false;isShow=false"></mask-layer>
    </transition>
  </div>
</template>

<script>
import layout from "../components/layout"
import maskLayer from "../components/maskLayer"
import { Indicator } from 'mint-ui'

export default {
  data: function(){
    return {
      list: [],
      defaultList: [],
      isLayout: false,
      isShow: false,
      state: 'login',
      allLoaded: false
    }
  },
  components: {
    'lay-out': layout,
    'mask-layer': maskLayer
  },
  mounted: function(){
    this.$common.scrollToTop();
    var self = this;
    Indicator.open();
    this.$http.get("/news/broadcast").then(function (response) {
      if(response.status === 200){
        self.list = response.data.dataList;
        Indicator.close();
      }
    })
    .catch(function (error) {
      console.log(error);
    });
  },
  watch: {
    list: function(par){
      for(let i = 0;i < 5;i++){
        this.defaultList.push(par[i]);
      }
    }
  },
  methods: {   
    close: function(pra){
      this.isLayout = pra;
      this.isShow = pra;
    },
    showJubao: function(event){
      var obj = event.currentTarget.nextSibling;
      var self = obj.nodeType == 3?obj.nextSibling:obj;
      self.style.display = 'block';
    },
    hideJubao: function(e){
      var obj = e.target;
      if(obj.className != 'jubao-box'){
        var arr = document.getElementsByClassName('jubao-box');
        var length = arr.length;
        for(let i = 0;i<length;i++){
          arr[i].style.display='none';
        }
      }
    },
    loadTop: function(){
      var self = this;
      this.list.length = 0,
      this.defaultList.length = 0;
      this.$http.get("/news/broadcast").then(function (response) {
        if(response.status === 200){
          self.list = response.data.dataList;
          self.$refs.loadmore.onTopLoaded();
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    loadBottom: function(){
      var defaultListLength = this.defaultList.length,
          max = Math.min(defaultListLength+5,this.list.length),
          self = this;
      for (let i = defaultListLength; i < max; i++) {
        self.defaultList.push(self.list[i]);
      }
      if(defaultListLength+5 >= self.list.length ){
        self.allLoaded = true;
      }
      self.$refs.loadmore.onBottomLoaded();
    }
  }
}
</script>

<style scoped>
.login {
  padding:15px 0;
  border-bottom: 1px solid #ddd;
}

.login img {
  width: 4rem;
  border-radius: 4rem;
  vertical-align: middle;
}

.login span {
  font-size: 1.3rem;
  vertical-align: middle;
  margin-left: 10px;
  color:#aaa;
}

.comment {
  margin: 30px 0;
}

.comment>img {
  width: 13%;
  border-radius: 100%;
}

.comment-content {
  width: 84%;
}

.comment-content h2 {
  font-size: 1.4rem;
  color:#aaa;
}

.comment-content h2 strong {
  color:#333;
  margin-right: 5px;
}

.comment-content h3 {
  padding:5px 0 10px 0;
  font-size: 1.2rem;
  color:#aaa;
}

.comment-content p {
  font-size: 1.4rem;
}

.comment-content>img {
  display: block;
  width: 100%;
  margin:15px 0 20px 0;
}

.comment-foot {
  font-size: 1.2rem;
  color:#aaa;
}

.comment-foot .zan img {
  width: 1.8rem;
  vertical-align: middle;
}

.comment-foot .zan span {
  vertical-align: middle;
  margin-left: 5px;
}

.comment-foot .jubao {
  position: relative;
}

.comment-foot .jubao span{
  font-weight: bolder;
}

.comment-foot .jubao .jubao-box {
  display: none;
  position: absolute;
  right:-10px;
  top:-5.5rem;
  width: 9rem;
  height: 4rem;
  line-height: 4rem;
  font-size: 1.4rem;
  color: #42bd56;
  border:1px solid #ddd;
  box-shadow: 0 0 1px #ddd;
  text-align: center;
  background: #fff;
  border-radius: 5px;
}

.login-layer {
  text-align: center;
}

.login-layer h2 {
  font-size: 1.5rem;
  padding-bottom: 25px;
}

.login-layer button,.login-layer input {
  width: 100%;
  padding:5px 0;
  font-size: 1.3rem;
  border-radius: 5px;
}

.login-layer button {
  color:#fff;
  background-color:#24be2f;
  margin-bottom: 10px;
}

.login-layer input {
  background-color: #fff;
  color:#24be2f;
  border:1px solid #24be2f;
  margin-bottom: 15px;
}

.login-layer span {
  font-size: 1.3rem;
  color:#24be2f;
}

.mask-enter-active,.mask-leave-active {
  transition: opacity .3s ease;
}

.mask-leave-active {
  transition: opacity .1s linear .5s;
}

.mask-enter,.mask-leave-active {
  opacity: 0;
}

</style>



