<template>
  <!-- 影院热映 -->
  <div class="filmClass">
    <h2>分类浏览</h2>
    <ul class="clearfix">
      <li v-for="(item,index) in link" class="clearfix" :class="index%2 != 0?'oddclass':''">
        <h3 class="pull-left">{{item}}</h3>
        <span class="pull-right">></span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:['link']
}
</script>

<style scoped>
.filmClass {
  margin-top: 30px;
}

.filmClass h2 {
  font-size: 20px;
  padding-bottom: 20px;
}

.filmClass ul li {
  float: left;
  width: 50%;
  font-size: 16px;
  border:1px solid #ddd;
  border-left: 0;
  margin-top: -1px;
  padding:10px 0;
}

.filmClass ul li h3 {
  color:#42bd56;
}

.filmClass ul li span {
  margin-right: 15px;
  color:#ddd;
}

.filmClass ul .oddclass {
  padding-left: 15px;
  border-right: 0;
}

</style>



