<template>
  <div v-if="defaultList.length === 0?false:true" id="contentList">
    <!-- <div v-if="contentShow" class="loading-content">
      <img src="../assets/picture/loading_green.c0f9be0.gif" alt=""/>
    </div> -->
    <!-- 内容循环 -->
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
      <div class="contentList-box" v-for="items in defaultList">
        <div class="pull-left contentList-title">
          <h2>{{items.contentTitle}}</h2>
          <p>{{items.contentTxt}}</p>
        </div>
        <img class="pull-right contentList-img" :src="items.contentPic" alt=""/>
        <div class="contentList-footer clearfix" style="clear:both">
          <span class="pull-left">{{items.contentTime}}</span>
        </div>
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'

export default {
  data: function(){
    return {
      list: [],
      defaultList: [],
      allLoaded: false
    }
  },
  mounted: function(){
    var self = this;
    Indicator.open();
    this.$http.get("/news/index").then(function (response) {
      if(response.status === 200){
        self.list = response.data.dataList;
        Indicator.close();
      }
    })
    .catch(function (error) {
      console.log(error);
    });
  },
  watch: {
    list: function(par){
      for(let i = 0;i < 10;i++){
        this.defaultList.push(par[i]);
      }
    }
  },
  methods: {
    loadTop: function(){
      var self = this;
      this.list.length = 0,
      this.defaultList.length = 0;
      this.$http.get("/news/index").then(function (response) {
        if(response.status === 200){
          self.list = response.data.dataList;
          self.$refs.loadmore.onTopLoaded();
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    loadBottom: function(){
      var defaultListLength = this.defaultList.length,
          max = Math.min(defaultListLength+10,this.list.length),
          self = this;
      for (let i = defaultListLength; i < max; i++) {
        self.defaultList.push(self.list[i]);
      }
      if(defaultListLength+10 >= self.list.length ){
        self.allLoaded = true;
      }
      self.$refs.loadmore.onBottomLoaded();
    }
  }

}
</script>

<style scoped>
#contentList {
  text-align: center;
}

.loading-content {
  margin-top: 25px;
  text-align: center;
}

.loading-content img {
  width: 3rem;
}

#contentList .contentList-box{
  padding: 30px 0;
  border-bottom:1px solid #e3e3e3;
}

#contentList .contentList-title {
  text-align: left;
  width: 70%;
}

#contentList .contentList-title h2 {
  color:#333;
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 10px;
}

#contentList .contentList-title p {
  font-size: 1.4rem;
  color:#aaa;
  text-align: justify;
}

#contentList .contentList-img {
  width: 25%;
}

#contentList .contentList-footer {
  padding-top: 15px;
  font-size: 1.3rem;
  color:#aaa;
}

</style>



